<template>
    <!---->
    <div class="bgcolor">
    <div class="baidu">
        <div class="span"><router-link to="/index"></router-link>></div>
        <div class="baidu1">
            <div class="ditu">
                <ul>
                    <li v-for='item of baidulist' @click="tiao" :data-lng="item.lng" :data-lat="item.lat">
                        <img :src="item.pic" alt="" width="11px" height="18px;" style="margin:3px;">
                        <span @click="tiao" :data-lng="item.lng" :data-lat="item.lat">{{item.ltitle}}</span>
                        <p>{{item.rtitle}}</p>
                        <router-link to="">></router-link>
                    </li>
                </ul>
            </div>
            <div id="XSDFXPage" class="XSDFXPage"></div>
        </div>
    </div>
</div>
</template>
<script>
export default {
    name:'',
    data () {
        return {
            dituip:{lng:120.103309,lat:30.320464},
            baidulist:[]
        }
    },
    mounted() {
        // API
        // Map
        var map = new BMap.Map("XSDFXPage",{enableMapClick:true});
        // ,
        map.centerAndZoom(new BMap.Point(116.4035,39.915), 11);
        // 
        map.addControl(new BMap.MapTypeControl());  
        //  
        map.setCurrentCity("");    
        //       
        map.enableScrollWheelZoom(true);

        /************************************************
        
        *************************************************/
        map.addControl(new BMap.ScaleControl({anchor:BMAP_ANCHOR_TOP_LEFT}));
        map.addControl(new BMap.NavigationControl());
        map.addControl(new BMap.NavigationControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,type:BMAP_NAVIGATION_CONTROL_SMALL}));
        /************************************************
        ZoomControl
        *************************************************/
        function ZoomControl() {
            //
            this.defaultAnchor = BMAP_ANCHOR_BOTTOM_RIGHT;
            this.defaultOffset = new BMap.Size(50,23);
        }
        //JavaScriptprototypeBMap.Control
        ZoomControl.prototype = new BMap.Control();
        //initializeDOM
        //div
        ZoomControl.prototype.initialize = function(map) {
            //DOM
            var div = document.createElement('div');
            //
            div.appendChild(document.createTextNode(''));
            //
            div.style.color = '#40C5CC'; 
            div.style.cursor = 'pointer';
            div.style.border = '3px solid gray';
            div.style.backgroundColor = '#eee';
            //
            div.onclick = function(e) {
                map.setZoom(map.getZoom() + 2);
            }
            //DOM
            map.getContainer().appendChild(div);
            //DOM
            return div;
        }
        //
        var myZoomCtrl = new ZoomControl();
        map.addControl(myZoomCtrl)

        /************************************************
        
        *************************************************/
        map.addControl(new BMap.CityListControl({
            anchor:BMAP_ANCHOR_BOTTOM_RIGHT,
            offset:new BMap.Size(130,23)
            // 
            // onChangeBefore: function(){
            //    alert('before');
            // },
            // 
            // onChangeAfter:function(){
            //   alert('after');
            // }
        }));
        /************************************************
        
        *************************************************/
         
        

        /************************************************
        
        *************************************************/
        // var beijingPosition=new BMap.Point(116.432045,39.910683),
        //     hangzhouPosition=new BMap.Point(120.129721,30.314429),
        //     taiwanPosition=new BMap.Point(121.491121,25.127053);
        // var point = [beijingPosition,hangzhouPosition,taiwanPosition];

        /************************************************
        
        *************************************************/
        var menu = new BMap.ContextMenu();

        var txtMenuItem = [
            {
                text:'',
                callback:function(){map.zoomIn()}
            },
            {
                text:'',
                callback:function(){map.zoomOut()}
            }
        ];
        for(var i=0; i < txtMenuItem.length; i++){
            menu.addItem(new BMap.MenuItem(txtMenuItem[i].text,txtMenuItem[i].callback,100));
        }
        map.addContextMenu(menu);
        var geolocation = new BMap.Geolocation(); //geolocation
        // SDK
        var ab=this.dituip;
        geolocation.enableSDKLocation();
        geolocation.getCurrentPosition(function(r){ //GeolocationResultnull
            if(this.getStatus() == BMAP_STATUS_SUCCESS){
                sessionStorage.setItem("SDKLng",r.point.lng);
                sessionStorage.setItem("SDKLat",r.point.lat);
                $.ajax({
                    url:'http://api.map.baidu.com/geocoder/v2/?ak=Ya2nSaqjT3vNrIgba1v4nfWzSxGdtgZD&location=' + r.point.lat + ',' + r.point.lng + '&output=json',
                    dataType: 'jsonp',
                    callback: 'BMap._rd._cbk43398',
                    success: function(res) {
                        var result = res.result,
                            addressComponent = result.addressComponent,
                            adcode = addressComponent.adcode
                        //
                        // 
                        // setTimeout(()=>{
                            map.panTo(new BMap.Point(ab.lng,ab.lat));
                        //}, 1000);
                        //setTimeout(()=>{
                            map.setZoom(14);
                        //},2000);
                        /************************************************
                        
                        *************************************************/
                        var pointGZ = new BMap.Point(ab.lng,ab.lat);
                        var pointHK = new BMap.Point(result.location.lng-0.07919341999992,result.location.lat+0.02374048197923);
                        //setTimeout(function(){
                            var polyline = new BMap.Polyline([pointGZ,pointHK],{strokeColor:"red",strokeWeight:5,strokeOpacity:0.5});
                            map.addOverlay(polyline);
                        //},3000);
                        // "http://developer.baidu.com/map/jsdemo/img/fox.gif"
                        var myIcon = new BMap.Icon("http://127.0.0.1:3500/img/baidu.png",new BMap.Size(20,64));
                        //
                        var marker = new BMap.Marker(pointHK,{icon:myIcon});
                        var marker1 = new BMap.Marker(pointGZ,{icon:myIcon});
                        //
                        map.addOverlay(marker);
                        map.addOverlay(marker1);
                        //
                        var a=result.formatted_address.split("")
                        var label = new BMap.Label(a[0]+"",{offset:new BMap.Size(-26.86,-26.14)});
                        marker.setLabel(label);
                        //
                        marker.addEventListener('click',function(){
                            var p = marker.getPosition();//
                            alert("" + p.lng + ',' + p.lat);
                        })
                    } ,
                    error:function(){

                    }
                });
            }
            else {
                alert('failed'+this.getStatus());
            }
        },{enableHighAccuracy: true});
    },
    created() {
        this.baidu();
    },
    methods: {
        baidu(){
            var url="http://127.0.0.1:3500/baidu";
            this.axios.get(url).then(result=>{
                console.log(result.data)
                this.baidulist=result.data;
            })
        },
        tiao(e){
            var lng=e.target.dataset.lng;
            var lat=e.target.dataset.lat;
            this.dituip.lng=lng;
            this.dituip.lat=lat;
            var map = new BMap.Map("XSDFXPage",{enableMapClick:true});
            map.centerAndZoom(new BMap.Point(120.140182,30.282985), 14);
            map.addControl(new BMap.MapTypeControl());  
            map.setCurrentCity(""); 
            map.enableScrollWheelZoom(true);
            map.addControl(new BMap.ScaleControl({anchor:BMAP_ANCHOR_TOP_LEFT}));
            map.addControl(new BMap.NavigationControl());
            map.addControl(new BMap.NavigationControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,type:BMAP_NAVIGATION_CONTROL_SMALL}));
            function ZoomControl() {
                this.defaultAnchor = BMAP_ANCHOR_BOTTOM_RIGHT;
                this.defaultOffset = new BMap.Size(50,23);
            }
            ZoomControl.prototype = new BMap.Control();
            ZoomControl.prototype.initialize = function(map) {
                var div = document.createElement('div');
                div.appendChild(document.createTextNode(''));
                div.style.color = '#40C5CC'; 
                div.style.cursor = 'pointer';
                div.style.border = '3px solid gray';
                div.style.backgroundColor = '#eee';
                div.onclick = function(e) {
                    map.setZoom(map.getZoom() + 2);
                }
                map.getContainer().appendChild(div);
                return div;
            }
            var myZoomCtrl = new ZoomControl();
            map.addControl(myZoomCtrl)
            map.addControl(new BMap.CityListControl({
                anchor:BMAP_ANCHOR_BOTTOM_RIGHT,
                offset:new BMap.Size(130,23)
            }));
            var menu = new BMap.ContextMenu();
            var txtMenuItem = [
                {
                    text:'',
                    callback:function(){map.zoomIn()}
                },
                {
                    text:'',
                    callback:function(){map.zoomOut()}
                }
            ];
            for(var i=0; i < txtMenuItem.length; i++){
                menu.addItem(new BMap.MenuItem(txtMenuItem[i].text,txtMenuItem[i].callback,100));
            }
            map.addContextMenu(menu);
            var geolocation = new BMap.Geolocation();
            var ab=this.dituip;
            geolocation.enableSDKLocation();
            geolocation.getCurrentPosition(function(r){ 
                if(this.getStatus() == BMAP_STATUS_SUCCESS){
                    sessionStorage.setItem("SDKLng",r.point.lng);
                    sessionStorage.setItem("SDKLat",r.point.lat);
                    $.ajax({
                        url:'http://api.map.baidu.com/geocoder/v2/?ak=Ya2nSaqjT3vNrIgba1v4nfWzSxGdtgZD&location=' + r.point.lat + ',' + r.point.lng + '&output=json',
                        dataType: 'jsonp',
                        callback: 'BMap._rd._cbk43398',
                        success: function(res) {
                            var result = res.result,
                                addressComponent = result.addressComponent,
                                adcode = addressComponent.adcode
                            // setTimeout(()=>{
                            //     map.panTo(new BMap.Point(ab.lng,ab.lat));
                            // }, 2000);
                            // setTimeout(()=>{
                            //     map.setZoom(14);
                            // },4000);
                            map.panTo(new BMap.Point(ab.lng,ab.lat));
                            map.setZoom(14);
                            var pointGZ = new BMap.Point(ab.lng,ab.lat);
                            var pointHK = new BMap.Point(result.location.lng-0.07919341999992,result.location.lat+0.02374048197923);
                            // setTimeout(function(){
                            //     var polyline = new BMap.Polyline([pointGZ,pointHK],{strokeColor:"red",strokeWeight:5,strokeOpacity:0.5});
                            //     map.addOverlay(polyline);
                            // },6000);
                            var polyline = new BMap.Polyline([pointGZ,pointHK],{strokeColor:"red",strokeWeight:5,strokeOpacity:0.5});
                                map.addOverlay(polyline);
                                var myIcon = new BMap.Icon("http://127.0.0.1:3500/img/baidu.png",new BMap.Size(20,64));
                            var marker = new BMap.Marker(pointHK,{icon:myIcon});
                            var marker1 = new BMap.Marker(pointGZ,{icon:myIcon});
                            map.addOverlay(marker);
                            map.addOverlay(marker1);
                            var a=result.formatted_address.split("")
                            var label = new BMap.Label(a[0]+"",{offset:new BMap.Size(-26.86,-26.14)});
                            marker.setLabel(label);
                            marker.addEventListener('click',function(){
                                var p = marker.getPosition();
                                alert("" + p.lng + ',' + p.lat);
                            })
                        } ,
                        error:function(){

                        }
                    });
                }
                else {
                    alert('failed'+this.getStatus());
                }
            },{enableHighAccuracy: true});
        }
    },
}
</script>
<style scoped>
    .bgcolor{
        background: #e9e9e9;
        padding-top:8rem;
    }
    .baidu{
        margin:0 auto;
        padding:2rem;
        width:1288px;
    }
    .baidu1{
        display:flex;
        background: #fff;
    }
    .span{
        color: #000;
        font-size: 14px;
        text-align: left;
        margin-bottom: 1rem;
    }
    .span a{
        color:#000;
    }
    .span a:hover{
        text-decoration: none;
        color:#3cc5b4;
    }
    .ditu{
        width:450px;
        height:700px;
        overflow-y:scroll; 
        overflow-x:hidden;
        table-layout: fixed;
        word-wrap:break-word;
        word-break:break-all;
    }
    .ditu ul{
        list-style: none;
        padding:0;
    }
    .ditu ul li{
        padding-left:1.5rem;
        padding-top:1.8rem;
        padding-bottom: 0.5rem;
        text-align: left;
        transition:all 0.5s !important; 
    }
    .ditu ul li:hover{
        background:#e9e9e9;
    }
    .ditu ul li span:hover{
        color: #3cc5b4;
        cursor: pointer;
    }
    .ditu ul li a{
        margin-left:20rem;
        color: #3cc5b4;
        font-size: 14px;
        transition:all 0.5s !important; 
    }
    .ditu ul li a:hover{
        text-decoration: none;
        color: #fb811a;
    }
    .XSDFXPage{
        width: 780px;
        height: 700px;
        background: #000;
        overflow: hidden;
        margin: 0;
        margin-left:1rem;
    }
</style>