<script>
import VDistpicker from 'v-distpicker'
    export default {
        components:{VDistpicker},
    data(){
        return{
            show1:false,
            show2:false,
            show3:false,
            show4:false,
           
        }   
    },
    mounted(){
        window.addEventListener('scroll',this.handleScroll)
    }, 
    methods:{
        handleScroll(){
           var scrollTop=window.pageYOffset ||document.documentElement.scrollTop||document.body.scrollTop;
          if( scrollTop<2000   ){
              this.show1=true;
            this.show2=false;
               this.show3=false;
               this.show4=false;
               
          }else if(scrollTop>=2000 &&scrollTop<2700){
              this.show1=false;
              this.show2=true;
               this.show3=false;
               this.show4=false;
                
          }else if(scrollTop>=2700 &&scrollTop<4800){
              this.show1=false;
              this.show2=false;
              this.show3=true;
               this.show4=false;
                
          }else if(scrollTop>=4800){
              this.show1=false;
              this.show2=false;
              this.show3=false;
              this.show4=true;
          }
          
        },
        selected(data){
            this.province=data.province.value;
            this.city=data.city.value;
        }
    }

  }
</script>
